<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list ref="tgList" :IsShowAction="true" :showCheckBox="true" :listType="['List']" ModuleName="Asset"
            SubModuleCode="MANAGE_PRODUCT" :PageSize="PageSize" :ViewList="ViewList" v-on:ApplyView="OnApplyView"
            IdentityColumn="asset_catalogue_id" :HeaderText="$t('AssetManagement')" :ListData="ProductData"
            :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :IsFilterApplied="searchCondition ? true : false" :IsSearchFieldsOnTop="false"
            :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata"
            v-on:RefreshManageView="FetchData">
            <template v-slot:slotdata="{ data }">
                <!-- {{ data.column }} -->
                <!-- {{ data.row }} -->
                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <a class="inactive-disabled" :title="data.row.status_name">
                        <span :class="{
                            'btn d-inline-block text-center text-truncate btn-success': data.row.status_name == 'Active', 'btn d-inline-block text-center text-truncate btn-danger': data.row.STATUS_NAME == 'Inactive',
                            'btn d-inline-block text-center text-truncate btn-info': data.row.status_name == 'In Stock', 'btn d-inline-block text-center text-truncate btn-warning': data.row.STATUS_NAME == 'In Use',
                            'btn d-inline-block text-center text-truncate btn-primary': data.row.status_name == 'In-Transit',
                            'btn d-inline-block text-center text-truncate btn-danger valid': data.row.status_name == 'Retired',
                            'btn d-inline-block text-center text-truncate btn-danger': data.row.status_name == 'Inactive'
                        }">
                            {{ data.row.status_name }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'number_of_item'">
                    <span class="text-dark" :title="data.row.number_of_item">
                        <i class="fa fa-list" aria-hidden="true"></i>
                        <span class="noti-circle noti-blue">{{ data.row.number_of_item > 0 ? data.row.number_of_item : 0
                            }}</span>
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'available_items'">
                    <span class="text-dark" :title="data.row.available_items">
                        <i class="fa fa-list-ul" aria-hidden="true"></i>
                        <span class="noti-circle noti-green">{{ data.row.available_items > 0 ? data.row.available_items
                            : 0
                            }}</span></span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'attachment_path'">
                    <div class="form-control p-0 border-0 text-center" v-if="data.row.attachment_path != null" alt=""
                        title="">
                        <img :src='data.row.attachment_path' style="max-height:60px;width:110px" title="" />
                    </div>
                    <div class="form-control p-0 border-0 text-center" v-if="data.row.attachment_path == null">
                        <img src='/Content/images/imagenotavailable.jpg' alt="No Image Available"
                            style="max-height:60px;width:110px" title="" />
                    </div>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'has_related_products'">
                    <div class="form-control p-0 border-0 text-center">
                        <span class="text-dark" v-if="data.row.has_related_products">
                            <a href="javascript:;" v-on:click="RelatedProductsPopup(data.row.asset_catalogue_id)"
                                class="relatedproduct" :id="data.row.asset_catalogue_id" title="Related Products "><i
                                    class="fa fa-sitemap text-primary" aria-hidden="true"></i></a>
                        </span>
                    </div>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'name'">
                    <span class="text-dark" v-if="data.row.asset_catalog_name">
                        <a href="javascript:;" v-on:click="clickEventOfColumn1(data.row.asset_catalogue_id)"
                            class="relatedproduct" :id="data.row.asset_catalogue_id"
                            :title="data.row.asset_catalog_name"><em class="text-primary" aria-hidden="true">{{
                                data.row.asset_catalog_name }}</em></a>
                    </span>

                </template>
            </template>

        </tg-list>


        <RelationPopup v-if=IsShowRelation></RelationPopup>
        <ClonePopup v-if="ShowClonePopup" :AssetCatCloneId="AssetCatCloneId"></ClonePopup>
        <AttachFilePopup v-if="ShowViewFilePopup" :AssetDetails="AssetDetails"></AttachFilePopup>
        <CheckAvailablity v-if="ShowCheckAvailablity" :CalledFrom="CalledFrom"></CheckAvailablity>
        <AddProduct :popupCondition="popupCondition" :FromPage="FromPage" v-if="ShowAddProduct"></AddProduct>
        <RelatedProducts v-if="ShowRelatedProducts" :AssetCatId="AssetCatId"></RelatedProducts>
        <searchFilter v-if="showFilter" submodulecode="MANAGE_PRODUCT" modulename="Asset"
            v-on:close="showFilter = false" v-bind:callbackfunction="FetchData" v-on:FilterData="FilterData">
            <h5 slot="header">{{ $t('Search') }}</h5>
        </searchFilter>
        <ManageView v-if="ShowManageView" submodulecode="MANAGE_PRODUCT" modulename="Asset"
            @refreshViewDropdown="refreshViewDropdown" v-on:close="ShowManageView = false"
            v-bind:callbackfunction="FetchData" :usertype="usertype">
            <h5 slot="header">{{ $t('Search') }}</h5>
        </ManageView>
        <modalquickviewdetails v-if="showquickviewModal" submodulecode="MANAGE_PRODUCT" modulename="Asset"
            v-on:close="showquickviewModal = false" :WorkOrderDetails="IsWorkOrderDetails">
        </modalquickviewdetails>
        <SearchFilter v-if="showFilter" submodulecode="MANAGE_PRODUCT" modulename="Asset"
            v-on:close="showFilter = false" v-bind:callbackfunction="FetchData" v-on:FilterData="FilterData">
            <!-- <h5 slot="header">{{ $t('Search') }}</h5> -->
        </SearchFilter>
    </section>
</template>

<script>
import Schema from "../AssetCatalog/ProductListingSchema";
import DataService from '../../service/DataService';
import RelationPopup from '../../components/Modal/AddRelationPopup.vue';
import ClonePopup from '../../components/Modal/CloneProduct.vue';
import CheckAvailablity from '../../components/Modal/checkAvailablity.vue';
import RelatedProducts from '../../components/Modal/RelatedProductsPopup.vue';
import AddProduct from '../../components/Modal/AddProduct.vue';
import AttachFilePopup from '../../components/Modal/ViewattachfilePopup.vue';
import viewlayout from "../../components/Common/view-layout.vue";
import ManageView from '../../components/Common/view-layout.vue'
import searchFilter from "../../components/Common/search-filter.vue";
export default {
    components: {
        RelationPopup,
        ClonePopup,
        CheckAvailablity,
        AddProduct,
        RelatedProducts,
        AttachFilePopup,
        viewlayout,
        ManageView,
        searchFilter
    },
    data() {
        return {
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            View_List: [],
            ViewList: [],
            ShowManageView: false,
            showquickviewModal: false,
            ViewListId: '',
            viewId: 0,
            searchCondition: '',
            searchConditionJson: '',
            showFilter: false,
            conditionForInstantSearch: "",
            LegendArray: [],
            ProductData: [],
            PageSize: 10,
            PageNumber: 1,
            PageSizeOption: null,
            StatusListForListing: [],
            Headers: [],

            // Headers: [{
            //     "COLUMN_NAME": 'ATTACHMENT_PATH',
            //     "DATA_TYPE": "image",
            //     "SORTABLE": false,
            //     "VISIBLE": true,
            //     "DISPLAY_NAME": this.$t('Image'),
            //     "DISPLAY_ORDER": 1,
            //     settings: {
            //         isInSlot: true,
            //     },
            // },
            // {
            //     "COLUMN_NAME": 'ASSET_TYPE',
            //     "DATA_TYPE": "text",
            //     "SORTABLE": true,
            //     "VISIBLE": true,
            //     "DISPLAY_NAME": this.$t('ItemNameType'),
            //     "DISPLAY_ORDER": 2
            // },
            // {
            //     "COLUMN_NAME": 'ITEM_CODE',
            //     "DATA_TYPE": "text",
            //     "SORTABLE": false,
            //     "VISIBLE": true,
            //     "DISPLAY_NAME": this.$t('ITEM_CODE'),
            //     "DISPLAY_ORDER": 3
            // },
            // {
            //     "COLUMN_NAME": 'NAME',
            //     "DATA_TYPE": "text",
            //     "SORTABLE": true,
            //     "VISIBLE": true,
            //     "DISPLAY_NAME": this.$t('PRODUCT_NAME'),
            //     "DISPLAY_ORDER": 4,
            //     settings: {
            //         isInSlot: true,
            //     }
            // },
            // {
            //     "COLUMN_NAME": 'STATUS_NAME',
            //     "DATA_TYPE": "text",
            //     "SORTABLE": true,
            //     "VISIBLE": true,
            //     "DISPLAY_NAME": this.$t('Status'),
            //     "DISPLAY_ORDER": 5,
            //     settings: {
            //         isInSlot: true,
            //     }
            // },
            // {
            //     "COLUMN_NAME": 'NUMBER_OF_ITEM',
            //     "DATA_TYPE": "text",
            //     "SORTABLE": false,
            //     "VISIBLE": true,
            //     "DISPLAY_NAME": this.$t('NUMBER_OF_ITEM'),
            //     "DISPLAY_ORDER": 6,
            //     settings: {
            //         isInSlot: true
            //     }
            // },

            // {
            //     "COLUMN_NAME": 'AVAILABLE_ITEMS',
            //     "DATA_TYPE": "text",
            //     "SORTABLE": false,
            //     "VISIBLE": true,
            //     "DISPLAY_NAME": this.$t('AVAILABLE_ITEMS'),
            //     "DISPLAY_ORDER": 7,
            //     settings: {
            //         isInSlot: true
            //     }
            // },
            // {
            //     "COLUMN_NAME": 'HAS_RELATED_PRODUCTS',
            //     "DATA_TYPE": "text",
            //     "SORTABLE": false,
            //     "VISIBLE": true,
            //     "DISPLAY_NAME": this.$t('HAS_RELATED_PRODUCTS'),
            //     "DISPLAY_ORDER": 8,
            //     settings: {
            //         isInSlot: true
            //     }
            // }

            // ],
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
            widgets: [],
            catalogId: '',
            legendProgress: [{
                class: '',
                backgroundColor: '#17a2b8',
                name: 'Draft'
            },
            {
                class: 'legend-Progress',
                backgroundColor: '',
                name: 'Approved'
            },
            {
                class: 'legend-pending',
                backgroundColor: '',
                name: 'Pending'
            },
            {
                class: '',
                backgroundColor: '#fe6847',
                name: 'SendForCorrection'
            },
            {
                class: '',
                backgroundColor: '#ee1313',
                name: 'Rejected'
            }
            ],
            IsShowRelation: false,
            ShowClonePopup: false,
            ShowViewFilePopup: false,
            ShowCheckAvailablity: false,
            ShowAddProduct: false,
            isLoading: false,
            ShowRelatedProducts: false,
            AssetCatId: '',
            AssetCatCloneId: '',
            AssetDetails: '',
            homeLink: false,
            privilegeParams: null,
            UserType: null,
            CalledFrom: 'ManageProdcut',
            appliedFilter: [],
            popupCondition: "",
            userid: null,
            usertype: null,
            companyId: null,

        }
    },

    async created() {
        var vueObj = this;

        this.userid = this.GetUserInfo.ID
        this.usertype = this.GetUserInfo.UserType
        this.companyId = this.GetUserInfo.CompanyId;
        // await vueObj.GetPageSize();
        vueObj.UserType = vueObj.GetUserInfo.UserType
        if (vueObj.UserType == 'PM') {
            vueObj.listheaderbuttons[8].isvisible = false
        }
        await vueObj.GetDdlRecords_ViewList();
        vueObj.privilegeParams = `controller=AssetCatalog&action=`;
        Schema.bindleftCommonSearchdropdown(vueObj, "Asset", "Location");
        Schema.bindleftCommonSearchdropdown(vueObj, "Asset", "Tag");
        Schema.bindleftCommonSearchdropdown(vueObj, "Asset", "Status");
        vueObj.UserType = vueObj.GetUserInfo.UserType;
        vueObj.LegendArray = [
            {
                class: 'legend-Progress colorboxsquare',
                text: vueObj.$t('AssetsHavingUniqueNames')
            },
            {
                class: 'legend-pending colorboxsquare',
                text: vueObj.$t('SampleData')
            }
        ]
        vueObj.PageSize = await vueObj.GetPageSize();
        await vueObj.CheckPrivilege();
    },
    methods: {
        // async GetPageSize(){ 
        //        var vm =this;
        //         var params='key=DefaultPageSize'
        //          DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {
        //             if(response.data!=null){
        //                 vm.PageSize =  parseInt(response.data);
        //             }                   
        //         });
        //       },
        async CheckPrivilege() {
            var vueObj = this;
            await DataService.CheckPrivilege(vueObj.privilegeParams + 'Index').then((response) => {
                if (response.data) {
                    vueObj.FetchData();
                } else {
                    vueObj.ShowAlert(vueObj.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });
        },
        RelatedProductsPopup(id) {
            var vm = this;
            vm.AssetCatId = id;
            vm.ShowRelatedProducts = true;
        },
        columnDataFormatter: function (row, column) {
            return row[column.COLUMN_NAME];
        },

        getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, searchCondition) {
            let params = {};
            if (pageNum) { params["pageNum"] = pageNum; }
            if (searchCondition) { params["searchCondition"] = searchCondition; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },

        async FetchData() {
            var vueObj = this;
            vueObj.isLoading = true;
            // var params='key=DefaultPageSize'
            //    await  DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {
            //         debugger
            //         if(response.data!=null){
            //             vm.PageSize =  parseInt(response.data);
            //         }                   
            //     });



            const params = this.getRequestParams(this.pageNum, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchCondition);

            params.pageSize = vueObj.PageSize;
            params.pageNum = vueObj.PageNumber;
            params.searchCondition = vueObj.searchCondition;
            if (vueObj.PageNumber) { params.pageNum = vueObj.PageNumber; }
            else { vueObj.PageNumber = params.pageNum = 1; }
            if (typeof vueObj.isPaged != 'undefined') { params.isPaged = vueObj.isPaged; }
            else { vueObj.isPaged = params.isPaged = 1; }
            params.isPaged = vueObj.isPaged;
            params.sortBy = vueObj.SortBy;
            params.sortExp = vueObj.SortExp;
            params.search = vueObj.search;
            var srchCondition = "";
            if (!this.isBlank(this.searchCondition)) {
                srchCondition = srchCondition + this.searchCondition;
            }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                // Adding "AC." at the beginning of srchCondition


                params.searchCondition = srchCondition;
                vueObj.searchCondition = srchCondition;
                if (vueObj.appliedFilter.length > 0) {
                    const removeFilterindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vueObj.listheaderbuttons[removeFilterindex].isvisible = true;
                    const openFilterPopUpindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vueObj.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter text-success';
                }
            }

            //var prams = `pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&${vueObj.searchConditionJson}&searchCondition=${vueObj.searchCondition}`;
            //params=`${params}&${vueObj.searchConditionJson}`;
            await DataService.AssetCatalogueListing(params).then(response => {
                if (response.data != null || response.data != 0) {

                    if (response.data.length > 0) {

                        if (JSON.parse(response.data) == 0) {
                            vueObj.noRecord = true;
                            vueObj.ProductData = [];
                            vueObj.TotalRecords = 0
                            vueObj.isLoading = false;
                            return;
                        }
                        vueObj.Headers = JSON.parse(response.data).schema;
                        vueObj.Headers.forEach((column) => {
                            if (column.COLUMN_NAME == 'attachment_path') {
                                column.settings = {
                                    isInSlot: true,
                                }
                            }
                            if (column.COLUMN_NAME == 'type') {
                                column.settings = {
                                    isInSlot: true,
                                    isSortable: true
                                }
                            }

                            if (column.COLUMN_NAME == 'name') {
                                column.settings = {
                                    isInSlot: true,
                                    isSortable: true
                                }
                            }

                            if (column.COLUMN_NAME == 'status_id') {
                                column.settings = {
                                    isInSlot: true,
                                    isSortable: true

                                }
                            }
                            if (column.COLUMN_NAME == 'number_of_item') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }

                            if (column.COLUMN_NAME == 'available_items') {
                                column.settings = {
                                    isInSlot: true,

                                }
                            }
                            if (column.COLUMN_NAME == 'create_unique_name_list') {
                                column.VISIBLE = true;
                                column.settings = {
                                    isInSlot: true
                                }
                            }

                            if (column.COLUMN_NAME == 'has_related_products') {
                                column.VISIBLE = true;
                                column.settings = {
                                    isInSlot: true
                                }
                            }


                        });


                        vueObj.ProductData = JSON.parse(response.data).data;
                        vueObj.ProductData.forEach((row) => {
                            row.rowDetailsUrl = '';
                            row.additionalClass = '';
                            row.isCheckBoxDisabled = false;
                            if (row.is_sample_data == true) {
                                row.additionalClass = 'sample-bar warning-bar';
                            }
                            if (row.has_unique_names) {
                                row.additionalClass = 'low-bar odd';
                            }
                            if (row.number_of_item != row.available_items) {
                                row.isCheckBoxDisabled = true;
                            }
                        })
                        vueObj.TotalRecords = JSON.parse(response.data).data[0]?.total;

                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        // $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        // $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        vueObj.listheaderbuttons[9].isdisabled = false;
                        // $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                        vueObj.isLoading = false;
                        // setTimeout(() => {
                        //     vueObj.checkboxSetting();

                        // }, 800);
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.ProductData = [];
                        vueObj.TotalRecords = 0
                        vueObj.isLoading = false;
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.ProductData = [];
                    vueObj.TotalRecords = 0
                    vueObj.isLoading = false;
                }
                vueObj.isLoading = false;
                // setTimeout(function () {
                //     vm.CheckBoxBootstrap();
                // }, 100);
                // setTimeout(function () {
                //     vueObj.ResponsiveDataTable('tablelistingdata');
                // }, 500);
                // setTimeout(function () {
                //     $("#tablelistingdata tbody td span[title|='true']").attr('title', '');
                //     $("#tablelistingdata tbody td span[title|='false']").attr('title', '');
                // }, 500);

            });
        },
        DeleteData: function () {
            var vm = this;
            var val = "";
            DataService.CheckPrivilege(vm.privilegeParams + 'Delete').then((response) => {
                if (response.data) {
                    $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                        if (val != "") {
                            vm.listheaderbuttons[9].isdisabled = false;
                            //$("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                        }

                    });
                    if (val.length > 0) {
                        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("asset_catalogue_id")), function () {
                            let url = `ids=${val}&tableName=TALYGEN_asset_catalogue&primaryKey=asset_catalogue_id`;
                            DataService.Delete(url).then(response => {
                                if (response.data) {
                                    $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                    $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                    //$("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                                    vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Product')]), "Success", true, vm.$t('Alert'));
                                }
                                else {
                                    vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
                                }
                                vm.FetchData();
                            });
                        });
                    }
                } else {
                    vm.ShowAlert(vm.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });

        },
        clickEventOfColumn1: function (ids, value) {
            var vm = this;
            DataService.CheckPrivilege(vm.privilegeParams + 'update').then((response) => {
                if (response.data) {
                    vm.$router.push({
                        //path: '/AssetCatalog/CustomEditProducts/'+ids
                        path: '/AssetCatalog/Update/' + ids
                    });
                } else {
                    vm.ShowAlert(vm.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });

        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            if (typeof value.searchConditionJson != 'undefined') {
                this.searchConditionJson = value.searchConditionJson;
            }
            else {
                this.searchConditionJson = "";
            }
            this.FetchData();
        },
        currentPageMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        GetPageSizeOption: function () {
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                list.push(i);
            }
            this.PageSizeOption = list;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "openFilterPopUp":
                    this.openFilterPopUp();
                    break;
                case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                    break
                case "addRelationShip":
                    this.AddRelationShipPopup();
                    break;
                case "CheckAvailablity":
                    this.OpenCheckAvailablity()
                    break;
                case "TransferItem":
                    this.OpenTransferItem()
                    break;
                case "OpenModelAddProduct":
                    this.OpenModelAddProduct()
                    break;
                case "ViewInTransitItems":
                    this.InTransitProducts()
                    break;
                case "ImportProduct":
                    this.ImportProduct()
                    break;
                case "DeletData":
                    this.DeleteData()
                    break;

                case "removeFilter":
                    var ths = this;
                    ths.searchCondition = '';
                    const removeFilterindex = ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    ths.listheaderbuttons[removeFilterindex].isvisible = false;
                    const openFilterPopUpindex = ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    ths.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter';
                    try {
                        ths.appliedFilter = [];
                    } catch (e) { };
                    ths.FetchData();
                    break;
                case 'ManageView':
                    this.ManageView();
                    break;
            }

        },
        ManageView: function () {
            this.privilegeParams = `controller=CRM&action=`;
            DataService.CheckPrivilege(this.privilegeParams + 'ManageView').then((response) => {
                if (response.data) {
                    var vm = this;
                    vm.ShowManageView = true;
                    setTimeout(function () {
                        $("#modalManageView").modal('show');
                    }, 1000);
                } else {
                    this.ShowAlert(this.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });
        },
        FilterData: function (pageNum, pageSize, sortBy, sortExp, searchcondition, filterItemData) {
            this.pageNum = pageNum;
            this.pageSize = pageSize;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.searchCondition = searchcondition;

            if (typeof filterItemData != null)
                this.appliedFilter.push(filterItemData);
            this.FetchData();
            this.ShowAlert("Filter has been applied successfully", "success", true, "Alert");

        },
        openFilterPopUp: function (source_id) {
            this.showFilter = true;
            // this.cancelOrRefresh();

        },
        refreshViewDropdown() {

            this.GetDdlRecords_ViewList();

        },
        DefaultView: function () {

            var vm = this;
            var obj = {
                ModuleName: 'Asset',
                SubmoduleCode: 'MANAGE_PRODUCT'
            };
            var objectPostString = JSON.stringify(obj);
            DataService.ClearView(objectPostString).then(function (response) {
                if (response.data != null) {
                    if (response.data.code == "1" || response.data.Code == "1") {
                        vm.ShowAlert(vm.$t("DeafultViewAppliedSuccessfully"), 'success', true, vm.$t('Alert'));
                        // vm.refreshViewDropdown();
                        setTimeout(function () {
                            vm.FetchData();
                        }, 3000);
                    }
                    else {
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                }
            });
        },
        OnApplyView(viewId) {
            var vm = this;
            if (viewId == 0) {
                vm.DefaultView()
            }
            else {
                var obj = {
                    ViewId: viewId,
                    ModuleName: 'Asset',
                    SubmoduleCode: 'MANAGE_PRODUCT'
                };
                var objectPostString = JSON.stringify(obj);
                DataService.ApplyView(objectPostString).then(function (response) {

                    if (response.data != null) {
                        if (response.data.code == "1" || response.data.Code == "1") {
                            // vm.GetDdlRecords_ViewList();
                            vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("View")]), 'success', true, vm.$t('Alert'));
                            vm.$emit('RefreshManageView');
                            setTimeout(function () {
                                vm.FetchData();
                            }, 3000);



                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                    }
                });

            }
        },
        OpenTransferItem() {
            var vm = this;
            DataService.CheckPrivilege(this.privilegeParams + 'TransferItem').then((response) => {
                if (response.data) {
                    window.location.href = "/AssetCatalog/TransferItem";
                } else {
                    this.ShowAlert(this.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });
        },
        ProductTransfer() {
            this.$router.push({ name: "Transferred Items" });
        },
        InTransitProducts() {
            var vm = this;
            DataService.CheckPrivilege(vm.privilegeParams + 'ViewInTransitItems').then((response) => {
                if (response.data) {
                    vm.$router.push({ path: '/AssetCatalog/ViewInTransitItems' });
                } else {
                    vm.ShowAlert(vm.$t('NotAuthorizedSection'), "failure", true, "Alert");
                    vm.$router.push({ path: '/unauthorized' });
                }
            });
        },
        OpenModelAddProduct() {
            var vm = this;
            DataService.CheckPrivilege(vm.privilegeParams + 'Add').then((response) => {
                if (response.data) {
                    vm.FromPage = 'ProductListing'
                    vm.popupCondition = "";
                    vm.ShowAddProduct = true;
                } else {
                    vm.ShowAlert(vm.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });

        },
        OpenCheckAvailablity() {
            var vm = this;
            DataService.CheckPrivilege(vm.privilegeParams + 'CheckProductAvailability').then((response) => {
                if (response.data) {
                    vm.ShowCheckAvailablity = true;
                } else {
                    vm.ShowAlert(vm.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });
        },
        RelatedInformation() {
            this.$router.push({ name: 'RelatedInformation' });
        },
        movetodepreciationlist: function () {
            this.$router.push({ name: "Depreciation" });
        },
        MovetoTransferredList: function () {
            this.$router.push({ name: "TransferredItemsList" });
        },
        ImportProduct: function () {
            var vm = this;
            if (vm.UserType == 'CA') {
                DataService.CheckPrivilege(vm.privilegeParams + 'ImportAssetItem').then((response) => {
                    if (response.data) {
                        vm.$router.push({ name: "ImportAssetitem" })
                    } else {
                        vm.ShowAlert(vm.$t('NotAuthorizedSection'), "failure", true, "Alert");
                    }
                });
            } else {
                vm.$router.push({ name: "ImportAssetitem" })
            }
        },
        AddRelationShipPopup() {
            DataService.CheckPrivilege(this.privilegeParams + 'AddRelationship').then((response) => {
                if (response.data == true) {
                    this.IsShowRelation = true;
                } else {
                    this.ShowAlert(this.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });
        },
        CloseAddRelationShipPopup() {
            var vm = this;
            vm.FetchData();
            vm.IsShowRelation = false;
        },
        OpenClonePopup(Id) {
            var vm = this;
            vm.AssetCatCloneId = Id;
            vm.ShowClonePopup = true;
        },
        OpenViewFilePopup(item) {
            var vm = this
            DataService.CheckPrivilege(vm.privilegeParams + 'ViewFiles').then((response) => {
                if (response.data) {
                    vm.AssetDetails = item;
                    vm.ShowViewFilePopup = true;
                } else {
                    vm.ShowAlert(vm.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });
        },
        actionButtonClick(callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case 'ProductList':
                    vm.ProductList();
                    break;
                case 'OpenClonePopup':
                    vm.OpenClonePopup(item.asset_catalogue_id);
                    break;
                case 'OpenViewFilePopup':
                    vm.OpenViewFilePopup(item);
                    break;
            }
        },
        // checkboxSetting() {
        //     $('#DeleteMultiple').addClass('disabled');
        //     $('.chkItems').change(function () {
        //         $('#tablelistingdata tbody .chkItems').each(function (index, tr) {
        //             if ($(this).prop("checked")) {
        //                 $('#DeleteMultiple').removeClass('disabled')
        //             }
        //         });
        //         if ($('#tablelistingdata tbody tr input ').not(':disabled').length == $('#tablelistingdata tbody tr :checked').length) {
        //             $('#tablelistingdata thead input:checkbox').prop('checked', true)
        //         }
        //         else {
        //             $('#tablelistingdata thead input:checkbox').prop('checked', false)
        //         }
        //     });
        // },
        CloseClonePopup() {
            var vm = this;
            vm.ShowClonePopup = false;
        },
        ClosePOPuP(item) {
            var vm = this;
            vm.ShowCheckAvailablity = false;
        },
        CloseRelatedProductsPopup() {
            var vm = this;
            vm.ShowRelatedProducts = false;
        },
        CloseViewFilePopup() {
            var vm = this;
            vm.ShowViewFilePopup = false;
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Clone':
                        action.isVisible = true;
                        break;
                    case 'Detail Products History':
                        action.isVisible = true;
                        break;
                    case 'View attached file(s)':
                        if (row.filecount == 0) {
                            action.isDisabled = true;
                        }

                        break;
                    default:
                        action.isVisible = true;
                        break;

                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        GetDdlRecords_ViewList: async function () {
            var vm = this;
            vm.ViewList = []
            var url = `moduleName=ASSET&subModuleCode=MANAGE_PRODUCT&viewId=0&pageSize=10&pageNum=1&sortBy=&sortExp=`;
            await DataService.GetViewList(url).then(function (response) {
                if (response.data && typeof (response.data.DATA) != 'undefined') {
                    vm.ViewList = response.data.DATA;

                    var chkVACTIVE = 0;
                    if (vm.ViewList == null || vm.ViewList == "") {
                        //
                    }
                    else {

                        vm.ViewList.forEach(function (item) {

                            if (item.IS_SLECTED_USER_ID > 0) {
                                chkVACTIVE = chkVACTIVE + 1;
                                vm.ViewListId = item.VIEW_ID;
                            }
                        });


                    }

                    var indx = 0;
                    if (chkVACTIVE > 0) {
                        vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "MANAGE_PRODUCT", "SUB_MODULE_CODE": "AssetCatalog", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "Asset", "CREATED_AT": "", "IS_SLECTED_USER_ID": 0, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                    }
                    else {
                        if (vm.ViewList == null || vm.ViewList == "") {

                            vm.ViewList.push({ "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                        }
                        else {
                            vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });

                        }
                    }
                    // console.log("viewlist", vm.ViewList);
                }
                else {
                    if (response.data == '' || response.data == null) {
                        vm.ViewListId = "0";
                    }
                }

                setTimeout(() => {
                    if (vm.ViewListId > 0) {
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = vm.ViewListId;
                    } else {
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = 0;
                    }
                }, 1000);
            });
        },
        // GetUserType(){
        //     var vm=this;                
        //     DataService.GetUserType()
        //     .then(function (response) {              
        //         if(response.data!='CA'){
        //             if(vm.listheaderbuttons[8].title =='Import Product'){
        //                 vm.listheaderbuttons[8].isvisible= false;
        //             }                    
        //         }
        //     });        
        // }
    },
    // updated: function () {
    //     this.BindActionButtonEvent();

    // },


}



</script>

